import React, { useState, useContext } from "react"
import styled from "styled-components"

import Overlay from "../overlay/Overlay"
import Input from "../inputs/PrimaryInput"
import Button from "../buttons/PrimaryButton"
import { CartContext } from "../../context/globalContext"
import generatePayLink from "../../utils/generatePayLink"

const FormWrapper = styled.form`
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
`

const Label = styled.label``

const Summary = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
`

const BuyForm = () => {
  const [email, setEmail] = useState("")
  const [currency, setCurrency] = useState("")

  const cartContext = useContext(CartContext)

  let totalPrice = 0
  cartContext.state.forEach(order => {
    totalPrice += order.price
  })

  let idList = []
  cartContext.state.forEach(order => {
    idList.push(order.id)
  })

  const handleClickBuy = e => {
    const payer = { email, currency }
    generatePayLink(idList, payer).then(res => (window.location.href = res))
    e.preventDefault()
  }

  return (
    <Overlay title="Buy">
      <FormWrapper onSubmit={e => handleClickBuy(e)}>
        <Label>
          Email:{" "}
          <Input
            placeholder="example@mail.com"
            align="left"
            onChange={e => setEmail(e.target.value)}
            value={email}
          />
          We will use it to deliver your order
        </Label>
        <Label>
          Currency:
          <Input value="USD" align="left" />
        </Label>
        <Summary>
          Total cost: {totalPrice}$<Button size="big">Go to payment</Button>
        </Summary>
      </FormWrapper>
    </Overlay>
  )
}

export default BuyForm
