const axios = require("axios")

const generatePayLink = (id, payer) => {
  const data = JSON.stringify({
    id: id,
    currency: payer.currency,
    payer: {
      first_name: undefined,
      last_name: undefined,
      email: payer.email,
      phone: undefined,
      address: {
        street: undefined,
        building_number: undefined,
        postcode: undefined,
        city: undefined,
        region: undefined,
        country: payer.country,
      },
    },
  })

  const config = {
    method: "post",
    url: "http://localhost:5001/hairstyles-gta5/us-central1/getPayLink",
    headers: {
      "Content-Type": "application/json",
    },
    data: data,
  }

  return axios(config)
    .then(res => {
      return res.data
    })
    .catch(err => {
      alert(err)
    })
}
export default generatePayLink
